:root {
  --primary-color: #387478;
  --secondary-color: #E2F1E7;
  --tertiary-color: #243642;
  --quaternary-color: #629584;
  --quinary-color: #fff;
}

/* Header */
.header {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 100vh;
  color: var(--tertiary-color);
  overflow: hidden;
  padding: 1rem;
  background: linear-gradient(
    105deg, 
    var(--secondary-color) 57%,
    var(--tertiary-color) 50%
    );
}

.menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  padding: 1rem;
  z-index: 3;
  font-weight: 700;
  background-color: #387478cc;
  color: var(--quinary-color);
}

.menu ul {
  display: flex;
  gap: 2rem;
  list-style: none;
  margin: 0;
  padding: 0;
}

.menu li {
  cursor: pointer;
}

.menu li a {
  text-decoration: none;
  font-size: 1.1rem;
  transition: color 0.3s ease;
}

.menu li a:hover {
  color: var(--tertiary-color);
}

.background-image {
  position: absolute;
  top: 0;
  right: 3rem;
  width: 41rem;
  height: 100%;
  object-fit: cover;
  z-index: 1;
}

.header-text {
  display: flex;
  position: relative;
  z-index: 2;
  margin-left: 2rem;
  flex-direction: column;
  align-items: flex-start;
  font-size: 1.8rem;
}

.header-text h1 {
  margin: 0.5em 0;
}

.header-text .subtitle {
  font-weight: 600;
}

.header-text p {
  margin: 0;
  color: black;
}

.header-text .description {
  font-size: 1.5rem;
  margin-top: 0.5rem;
  max-width: 500px;
  line-height: 1.5;
  width: 400px;
}

/* About */
.about-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 20px;
}

.about-container {
  display: flex;
  gap: 2rem;
  max-width: 75rem;
}

.profile-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.profile-image {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  margin-bottom: 1rem;
  background-color: var(--primary-color);
}

.personal-info {
  text-align: center;
  font-size: 1.5rem;
  width: max-content;
}

.personal-info h2 {
  margin: 0.5rem 0;
  color: var(--tertiary-color);
}

.personal-info h3 {
  color: var(--tertiary-color);
}

.personal-info p:nth-child(3) {
  font-size: 1.3rem;
}
.personal-info p {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--tertiary-color);
}

.description-section {
  flex: 2;
}

.description-section p {
  text-indent: 1rem;
  text-align: justify;
  line-height: 2rem;
  font-size: 1.4rem;
  font-weight: 500;
  margin: 3rem 0;
  color: var(--tertiary-color);
}

.heading {
  text-align: center;
    font-size: 2rem;
    border: solid var(--tertiary-color);
    border-radius: 20px;
    width: fit-content;
    margin: 1rem auto;
    color: var(--tertiary-color);
    padding: 0.5rem 1rem;
}

.skills-section {
  margin-top: 1.5rem;
}

.skills-section h1 {
  color: var(--tertiary-color);
  font-size: 1.5rem;
  display: flex;
  border: dashed;
  width: fit-content;
  padding: 0 2rem;
  border-radius: 15px;
}


.skills-section span{
  padding-left: 0.5rem;
  font-weight: 600;
  color: var(--tertiary-color);
}

.skill {
  margin-bottom: 0.8rem;
}

.skill-bar {
  background: #ddd;
  border-radius: 15px;
  overflow: hidden;
  height: 25px;
}

.skill-level {
  background: var(--primary-color);
  color: var(--quinary-color);
  padding-left: 0.5rem;
  height: 100%;
  display: flex;
  align-items: center;
  font-size: 1.2rem;
  border-radius: 15px;
}

/* Resume */
.resume-section {
  background-color: var(--tertiary-color);
  text-align: center;
  padding: 2rem;
}

.resume-heading {
  color: var(--secondary-color);
  border: solid var(--secondary-color);
}

.resume-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 2px solid var(--secondary-color);
  background-color: var(--secondary-color);
  border-radius: 20px;
  padding: 2rem;
  width: 60%;
  margin: auto;
}

.resume-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  width: 100%;
}

.resume-image-container {
  width: 200px;
  height: 225px;
  background-color: var(--primary-color);
  clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
  overflow: hidden;
  flex-shrink: 0;
}

.resume-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.resume-content {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  text-align: left;
  width: 100%;
}

.resume-detail {
  font-size: 1.2rem;
  margin: 0.5rem 0;
  width: 100%;
  padding-right: 1rem;
}

.resume-divider {
  border: none;
  border-top: 2px solid var(--primary-color);
  width: 100%;
  margin: 1rem 0;
}

.resume-card {
  width: 100%;
  text-align: left;
}

.resume-title {
  color: var(--secondary-color);
  background-color: var(--primary-color);
  padding: 0.5rem 1rem;
  border-radius: 20px;
  width: fit-content;
  margin-bottom: 1rem;
}

.resume-education-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.resume-education {
  font-size: 1.2rem;
  margin: 0.5rem 0;
  padding-left: 1rem;
  border-left: 4px solid var(--primary-color);
}

.resume-experience-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
}

.resume-experience {
  font-size: 1.2rem;
  margin: 0.5rem 0;
  padding-left: 1rem;
  border-left: 4px solid var(--primary-color);
}

.resume-experience p {
  margin: 0;
  text-align: justify;
}

.resume-training-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
}

.resume-training {
  font-size: 1.2rem;
  margin: 0.5rem 0;
  padding-left: 1rem;
  border-left: 4px solid var(--primary-color);
}

.resume-training p {
  margin: 0;
  text-align: justify;
}



/* Projects */

.projects-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  max-height: 100%;
  overflow-y: auto;
}

.project-card {
  background-color: var(--quinary-color);
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  padding: 1rem;
  width: 300px;
  text-align: center;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 450px;
}

.project-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 10px;
}

.project-info {
  position: absolute;
  bottom: 20px;
  left: 10px;
  right: 10px;
  padding: 10px;
  border-radius: 10px;
}

.project-title {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

.project-description {
  font-size: 1rem;
  margin-bottom: 1rem;
}

.project-link {
  display: inline-block;
  padding: 0.5rem 1rem;
  background-color: var(--primary-color);
  color: var(--quinary-color);
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.project-link:hover {
  background-color: var(--quaternary-color);
}

/* Contact */
.contact-section {
  padding: 2rem;
  text-align: center;
  background-color: var(--tertiary-color);
  height: 100vh;
}

.contact-container {
  max-width: 600px;
  margin: 0 auto;
}

.contact-heading {
  color: var(--secondary-color);
  border: solid var(--secondary-color);
}

.contact-info {
  margin: 3.5rem 0;
}

.contact-info p {
  font-size: 1.2rem;
  color: var(--secondary-color);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.icon {
  color: var(--primary-color);
}

.icon:hover {
  color: var(--tertiary-color);
}

.social-media {
  margin: 1.5rem 0;
}

.social-icon {
  color: var(--primary-color);
  font-size: 2rem;
  margin: 0 0.5rem;
  transition: color 0.3s;
}

.social-icon:hover {
  color: var(--tertiary-color);
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.contact-form input,
.contact-form textarea {
  padding: 0.75rem;
  font-size: 1rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  width: 100%;
}

.submit-btn {
  padding: 0.75rem;
  font-size: 1rem;
  color: none;
  background-color: var(--quaternary-color);
  border: none;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 0.3s;
  width: 35%;
  margin: auto;
}

.submit-btn:hover {
  background-color: var(--secondary-color);
}

/* Media Queries */
@media (max-width: 1024px) {
  .header {
    font-size: 1.5rem;
    background: linear-gradient(
      105deg, 
      var(--secondary-color) 50%,
      var(--tertiary-color) 50%
      );
  }

  .background-image {
    bottom: 0;
    right: 0;
    top: auto;
    height: 90%;
}

.about-section{
  padding: 2rem;
  margin: 1rem 3rem;
  font-size: 1.5rem;
}

.description-section p {
  font-size: 1.5rem;
  line-height: 1.8;
}
}